import {ParamType} from './../../../../../@polygonjs/polygonjs/src/engine/poly/ParamType';
import type {MeshBasicBuilderMatNode} from './../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshBasicBuilder';
import type {MaterialsNetworkSopNode} from '../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MaterialsNetwork';
import type {GeoObjNode} from '../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Geo';
import type {EventsNetworkSopNode} from '../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/EventsNetwork';
import type {PerspectiveCameraObjNode} from '../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/PerspectiveCamera';
import type {RootManagerNode} from '../../../../../@polygonjs/polygonjs/src/engine/nodes/manager/Root';
import type {ExampleData, DataCallbackOptions} from '../../Common';
import {AttribClass} from '../../../../../@polygonjs/polygonjs/src/core/geometry/Constant';

export function createData(options: DataCallbackOptions): ExampleData {
	const {scene} = options;
	const root = scene.root();
	function create_geo1(parentNode: RootManagerNode) {
		var geo1 = parentNode.createNode('geo');
		geo1.setName('geo1');
		function create_MAT(geo1: GeoObjNode) {
			var MAT = geo1.createNode('materialsNetwork');
			MAT.setName('MAT');
			function create_meshBasicBuilder1(MAT: MaterialsNetworkSopNode) {
				var meshBasicBuilder1 = MAT.createNode('meshBasicBuilder');
				meshBasicBuilder1.setName('meshBasicBuilder1');
				function create_attribute1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var attribute1 = meshBasicBuilder1.createNode('attribute');
					attribute1.setName('attribute1');
					attribute1.uiData.setPosition(-600, 450);
					attribute1.p.name.set('id');
					attribute1.params.postCreateSpareParams();
					attribute1.params.runOnSceneLoadHooks();
					return attribute1;
				}
				function create_constant1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var constant1 = meshBasicBuilder1.createNode('constant');
					constant1.setName('constant1');
					constant1.uiData.setPosition(50, -200);
					constant1.p.type.set(4);
					constant1.p.color.set([1, 0, 0]);

					constant1.p.asColor.set(true);
					constant1.params.postCreateSpareParams();
					constant1.params.runOnSceneLoadHooks();
					return constant1;
				}
				function create_constant2(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var constant2 = meshBasicBuilder1.createNode('constant');
					constant2.setName('constant2');
					constant2.uiData.setPosition(50, -50);
					constant2.p.type.set(4);
					constant2.p.color.set([0, 1, 0]);

					constant2.p.asColor.set(true);
					constant2.params.postCreateSpareParams();
					constant2.params.runOnSceneLoadHooks();
					return constant2;
				}
				function create_fit1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var fit1 = meshBasicBuilder1.createNode('fit');
					fit1.setName('fit1');
					fit1.uiData.setPosition(50, 200);
					fit1.addParam(ParamType.FLOAT, 'val', 0, {spare: true, editable: false});
					fit1.addParam(ParamType.FLOAT, 'srcMin', 0, {spare: true, editable: true});
					fit1.params.get('srcMin')!.set(-1);
					fit1.addParam(ParamType.FLOAT, 'srcMax', 1, {spare: true, editable: true});
					fit1.addParam(ParamType.FLOAT, 'destMin', 0, {spare: true, editable: true});
					fit1.addParam(ParamType.FLOAT, 'destMax', 1, {spare: true, editable: true});
					fit1.params.postCreateSpareParams();
					fit1.params.runOnSceneLoadHooks();
					return fit1;
				}
				function create_floatToVec2_1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var floatToVec2_1 = meshBasicBuilder1.createNode('floatToVec2');
					floatToVec2_1.setName('floatToVec2_1');
					floatToVec2_1.uiData.setPosition(-400, 450);
					floatToVec2_1.params.postCreateSpareParams();
					floatToVec2_1.params.runOnSceneLoadHooks();
					return floatToVec2_1;
				}
				function create_globals1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var globals1 = meshBasicBuilder1.createNode('globals');
					globals1.setName('globals1');
					globals1.uiData.setPosition(-350, -100);
					globals1.params.postCreateSpareParams();
					globals1.params.runOnSceneLoadHooks();
					return globals1;
				}
				function create_mix1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var mix1 = meshBasicBuilder1.createNode('mix');
					mix1.setName('mix1');
					mix1.uiData.setPosition(300, 100);
					mix1.addParam(ParamType.VECTOR3, 'value0', [0, 0, 0], {spare: true, editable: false});
					mix1.addParam(ParamType.VECTOR3, 'value1', [0, 0, 0], {spare: true, editable: false});
					mix1.addParam(ParamType.FLOAT, 'blend', 0.5, {spare: true, editable: false});
					mix1.params.postCreateSpareParams();
					mix1.params.runOnSceneLoadHooks();
					return mix1;
				}
				function create_multAdd1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var multAdd1 = meshBasicBuilder1.createNode('multAdd');
					multAdd1.setName('multAdd1');
					multAdd1.uiData.setPosition(-150, 200);
					multAdd1.addParam(ParamType.FLOAT, 'value', 0, {spare: true, editable: false});
					multAdd1.addParam(ParamType.FLOAT, 'preAdd', 0, {spare: true, editable: false});
					multAdd1.addParam(ParamType.FLOAT, 'mult', 1, {spare: true, editable: true});
					multAdd1.params.get('mult')!.set(4);
					multAdd1.addParam(ParamType.FLOAT, 'postAdd', 0, {spare: true, editable: true});
					multAdd1.params.postCreateSpareParams();
					multAdd1.params.runOnSceneLoadHooks();
					return multAdd1;
				}
				function create_output1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var output1 = meshBasicBuilder1.createNode('output');
					output1.setName('output1');
					output1.uiData.setPosition(550, 100);
					output1.params.postCreateSpareParams();
					output1.params.runOnSceneLoadHooks();
					return output1;
				}
				function create_random1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var random1 = meshBasicBuilder1.createNode('random');
					random1.setName('random1');
					random1.uiData.setPosition(-300, 450);
					random1.params.postCreateSpareParams();
					random1.params.runOnSceneLoadHooks();
					return random1;
				}
				function create_round1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var round1 = meshBasicBuilder1.createNode('round');
					round1.setName('round1');
					round1.uiData.setPosition(-500, 450);
					round1.addParam(ParamType.FLOAT, 'in', 0, {spare: true, editable: false});
					round1.params.postCreateSpareParams();
					round1.params.runOnSceneLoadHooks();
					return round1;
				}
				function create_sin1(meshBasicBuilder1: MeshBasicBuilderMatNode) {
					var sin1 = meshBasicBuilder1.createNode('sin');
					sin1.setName('sin1');
					sin1.uiData.setPosition(-50, 200);
					sin1.addParam(ParamType.FLOAT, 'radians', 0, {spare: true, editable: false});
					sin1.params.postCreateSpareParams();
					sin1.params.runOnSceneLoadHooks();
					return sin1;
				}
				var attribute1 = create_attribute1(meshBasicBuilder1);
				var constant1 = create_constant1(meshBasicBuilder1);
				var constant2 = create_constant2(meshBasicBuilder1);
				var fit1 = create_fit1(meshBasicBuilder1);
				var floatToVec2_1 = create_floatToVec2_1(meshBasicBuilder1);
				var globals1 = create_globals1(meshBasicBuilder1);
				var mix1 = create_mix1(meshBasicBuilder1);
				var multAdd1 = create_multAdd1(meshBasicBuilder1);
				var output1 = create_output1(meshBasicBuilder1);
				var random1 = create_random1(meshBasicBuilder1);
				var round1 = create_round1(meshBasicBuilder1);
				var sin1 = create_sin1(meshBasicBuilder1);
				fit1.setInput('val', sin1, 'val');
				floatToVec2_1.setInput('x', round1, 'val');
				mix1.setInput('value0', constant1, 'val');
				mix1.setInput('value1', constant2, 'val');
				mix1.setInput('blend', fit1, 'val');
				multAdd1.setInput('value', globals1, 'time');
				multAdd1.setInput('preAdd', random1, 'rand');
				output1.setInput('color', mix1, 'mix');
				random1.setInput('seed', floatToVec2_1, 'vec2');
				round1.setInput('in', attribute1, 'val');
				sin1.setInput('radians', multAdd1, 'val');
				meshBasicBuilder1.uiData.setPosition(0, -50);
				meshBasicBuilder1.params.postCreateSpareParams();
				meshBasicBuilder1.params.runOnSceneLoadHooks();
				return meshBasicBuilder1;
			}
			var meshBasicBuilder1 = create_meshBasicBuilder1(MAT);
			MAT.uiData.setPosition(-250, 150);
			MAT.params.postCreateSpareParams();
			MAT.params.runOnSceneLoadHooks();
			return {MAT, meshBasicBuilder1};
		}
		function create_attribId1(geo1: GeoObjNode) {
			var attribId1 = geo1.createNode('attribId');
			attribId1.setName('attribId1');
			attribId1.uiData.setPosition(100, -50);
			attribId1.p.idn.set(false);
			attribId1.params.postCreateSpareParams();
			attribId1.params.runOnSceneLoadHooks();
			return attribId1;
		}
		function create_attribPromote1(geo1: GeoObjNode) {
			var attribPromote1 = geo1.createNode('attribPromote');
			attribPromote1.setName('attribPromote1');
			attribPromote1.uiData.setPosition(0, 250);
			attribPromote1.setAttribClassFrom(AttribClass.OBJECT);
			attribPromote1.setAttribClassTo(AttribClass.POINT);
			attribPromote1.p.name.set('id');
			attribPromote1.params.postCreateSpareParams();
			attribPromote1.params.runOnSceneLoadHooks();
			return attribPromote1;
		}
		function create_copy1(geo1: GeoObjNode) {
			var copy1 = geo1.createNode('copy');
			copy1.setName('copy1');
			copy1.uiData.setPosition(0, 100);
			copy1.p.copyAttributes.set(true);
			copy1.p.attributesToCopy.set('id');
			copy1.params.postCreateSpareParams();
			copy1.params.runOnSceneLoadHooks();
			return copy1;
		}
		function create_material1(geo1: GeoObjNode) {
			var material1 = geo1.createNode('material');
			material1.setName('material1');
			material1.uiData.setPosition(0, 400);
			material1.flags.display.set(true);
			material1.p.material.set('../MAT/meshBasicBuilder1');
			material1.params.postCreateSpareParams();
			material1.params.runOnSceneLoadHooks();
			return material1;
		}
		function create_plane1(geo1: GeoObjNode) {
			var plane1 = geo1.createNode('plane');
			plane1.setName('plane1');
			plane1.uiData.setPosition(100, -250);
			plane1.p.size.set([4, 4]);
			plane1.params.postCreateSpareParams();
			plane1.params.runOnSceneLoadHooks();
			return plane1;
		}
		function create_sphere1(geo1: GeoObjNode) {
			var sphere1 = geo1.createNode('sphere');
			sphere1.setName('sphere1');
			sphere1.uiData.setPosition(-100, -50);
			sphere1.p.radius.set(0.36);
			sphere1.params.postCreateSpareParams();
			sphere1.params.runOnSceneLoadHooks();
			return sphere1;
		}
		var {MAT, meshBasicBuilder1} = create_MAT(geo1);
		var attribId1 = create_attribId1(geo1);
		var attribPromote1 = create_attribPromote1(geo1);
		var copy1 = create_copy1(geo1);
		var material1 = create_material1(geo1);
		var plane1 = create_plane1(geo1);
		var sphere1 = create_sphere1(geo1);
		attribId1.setInput(0, plane1);
		attribPromote1.setInput(0, copy1);
		copy1.setInput(0, sphere1);
		copy1.setInput(1, attribId1);
		material1.setInput(0, attribPromote1);
		geo1.uiData.setPosition(-50, -150);
		geo1.flags.display.set(true);
		geo1.params.postCreateSpareParams();
		geo1.params.runOnSceneLoadHooks();
		return {geo1, MAT, meshBasicBuilder1};
	}
	function create_grid(parentNode: RootManagerNode) {
		var grid = parentNode.createNode('geo');
		grid.setName('grid');
		function create_planeHelper1(grid: GeoObjNode) {
			var planeHelper1 = grid.createNode('planeHelper');
			planeHelper1.setName('planeHelper1');
			planeHelper1.uiData.setPosition(0, 0);
			planeHelper1.flags.display.set(true);
			planeHelper1.params.postCreateSpareParams();
			planeHelper1.params.runOnSceneLoadHooks();
			return planeHelper1;
		}
		create_planeHelper1(grid);
		grid.uiData.setPosition(-50, -250);
		grid.flags.display.set(true);
		grid.params.postCreateSpareParams();
		grid.params.runOnSceneLoadHooks();
		return grid;
	}
	function create_perspectiveCamera1(parentNode: RootManagerNode) {
		var perspectiveCamera1 = parentNode.createNode('perspectiveCamera');
		perspectiveCamera1.setName('perspectiveCamera1');
		function create_events1(perspectiveCamera1: PerspectiveCameraObjNode) {
			var events1 = perspectiveCamera1.createNode('eventsNetwork');
			events1.setName('events1');
			function create_cameraOrbitControls1(events1: EventsNetworkSopNode) {
				var cameraOrbitControls1 = events1.createNode('cameraOrbitControls');
				cameraOrbitControls1.setName('cameraOrbitControls1');
				cameraOrbitControls1.uiData.setPosition(150, 50);
				cameraOrbitControls1.params.postCreateSpareParams();
				cameraOrbitControls1.params.runOnSceneLoadHooks();
				return cameraOrbitControls1;
			}
			create_cameraOrbitControls1(events1);
			events1.uiData.setPosition(-200, 50);
			events1.params.postCreateSpareParams();
			events1.params.runOnSceneLoadHooks();
			return events1;
		}
		create_events1(perspectiveCamera1);
		perspectiveCamera1.uiData.setPosition(-200, 100);
		perspectiveCamera1.flags.display.set(true);
		perspectiveCamera1.p.t.set([3.0161526633531355, 1.1787657307191874, 2.814738208647524]);
		perspectiveCamera1.p.r.set([-22.723127930556515, 44.66532130223666, 16.403892417511617]);
		perspectiveCamera1.p.controls.set('./events1/cameraOrbitControls1');
		perspectiveCamera1.params.postCreateSpareParams();
		perspectiveCamera1.params.runOnSceneLoadHooks();
		return perspectiveCamera1;
	}
	var {meshBasicBuilder1} = create_geo1(root);
	create_grid(root);
	var perspectiveCamera1 = create_perspectiveCamera1(root);
	scene.play();
	return {
		scene,
		cameraPath: perspectiveCamera1.path(),
		focusedNodes: meshBasicBuilder1.children(),
		autoLayout: false,
	};
}
